.list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 20px;
  list-style: none;
  padding: 0;
}

.item {
  /* width: calc((100% - 60px) / 4); */
  display: flex;
  gap: 16px;
  justify-content: space-between;
  align-items: center;
}

.listBtn {
  height: 40px;
  width: 80px;
  border: none;
  background-color: blue;
  border-radius: 4px;
  color: white;
  font-size: 18px;
  cursor: pointer;
}

.text {
  font-size: 24px;
  font-style: italic;
}

@media screen and (min-width: 700px) and (max-width: 1000px) {
  .list {
    flex-direction: row;
  }
  .item {
    width: calc((100% - 20px) / 2);
  }
}

@media screen and (min-width: 1001px) {
  .list {
    flex-direction: row;
  }
  .item {
    width: calc((100% - 40px) / 3);
  }
}
